.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.liveWrapper{
  display: flex;
  padding-top: 30px;
}

.liveBox{
  margin: 0 auto;
    max-width: 1280px;
    display: flex;
    border-radius: 20px;
    overflow: hidden;
    box-shadow: 0px 5px 10px #00000012;
}
.str-chat-channel {
  max-height: 500px !important;
}

.navbarRouter img{
  height:30px;
}

.navbarRouter {
  box-shadow: 5px 1px 10px #00000012;
}

.profile-pic{
  border-radius: 100px;
  width: 60px;
  height: 60px;
}

.description{
  text-align: left;
  padding: 15px 20px;
}


.who{
  padding-left: 20px;
}

.who h3, h4, h5{
  margin: 0;
}

.who h3{
  font-weight: 600;
    font-size: 18px;
}

.who h4{
  font-weight: 200;
  padding: 3px 0px;
}

.who h5{
  font-weight: 200;
  color: #ee6b6b;
}
.descSection{
  display: flex;
}

.verified{
  width: 13px;
}